import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Icon } from 'react-icons-kit';
import { ic_exposure, ic_clear } from 'react-icons-kit/md';

@inject('gold')
@observer
class GoldCalculator extends Component {
  handleGoldToggleTrue = () => {
    const { gold } = this.props;
    gold.actGoldToggleTrue();
  };
  handleGoldToggleFalse = () => {
    const { gold } = this.props;
    gold.actGoldToggleFalse();
  };

  handleGoldValue = e => {
    const { gold } = this.props;
    gold.actGoldValue(e.target.value);
  };

  render() {
    const {
      handleGoldToggleTrue,
      handleGoldToggleFalse,
      handleGoldValue
    } = this;
    const { gold } = this.props;

    return (
      <div className="items">
        {!gold.calculatorToggle && (
          <div className="fl_right">
            <button type="button" onClick={handleGoldToggleTrue}>
              <p className="icon">
                <Icon icon={ic_exposure} />
              </p>
              <p className="text">그램(g) 계산기</p>
            </button>
          </div>
        )}
        {gold.calculatorToggle && (
          <div className="fl_left">
            <table>
              <colgroup>
                <col style={{ width: '10%' }} />
                <col style={{ width: '25%' }} />
                <col style={{ width: '10%' }} />
                <col style={{ width: '10%' }} />
                <col style={{ width: '25%' }} />
                <col style={{ width: '10%' }} />
                <col />
              </colgroup>
              <tbody>
                <tr>
                  <td colSpan="5">
                    <input
                      type="text"
                      placeholder="숫자를 입력하세요."
                      onChange={handleGoldValue}
                    />
                  </td>
                  <td className="bg2 fs20">g</td>
                  <td rowSpan="2" className="bg">
                    <button
                      type="button"
                      className="btn"
                      onClick={handleGoldToggleFalse}
                    >
                      <Icon icon={ic_clear} />
                    </button>
                  </td>
                </tr>
                <tr>
                  <th>살때</th>
                  <td className="tar">
                    {String(Math.floor(gold.buyTotal)).replace(
                      /(\d)(?=(?:\d{3})+(?!\d))/g,
                      '$1,'
                    )}
                  </td>
                  <td className="bg2">원</td>
                  <th className="bgr">팔때</th>
                  <td className="tar">
                    {String(Math.floor(gold.sellTotal)).replace(
                      /(\d)(?=(?:\d{3})+(?!\d))/g,
                      '$1,'
                    )}
                  </td>
                  <td className="bg2">원</td>
                </tr>
              </tbody>
            </table>
          </div>
        )}
      </div>
    );
  }
}

export default GoldCalculator;
